import React from 'react'

const Card = props => (
    <div class="w-full shadow-lg rounded-lg">
    <div class="h-48 flex-none bg-cover rounded-t-lg text-center overflow-hidden" style={{backgroundImage: "url(https://tailwindcss.com/img/card-left.jpg)"}} title={props.alt}>
    </div>
    <div class="bg-white rounded-b-lg p-4 flex flex-col justify-between leading-normal">
      <div class="">
        <div class="text-gray-900 font-bold text-xl mb-2">{props.title}</div>
        <p class="text-gray-700 text-base">{props.text}</p>
      </div>
    </div>
  </div> 
)

export default Card


